<!--  -->
<template>
  <div class="page-wrap" v-if="code==200&&total>pageSize">
    <el-pagination @current-change="change" :page-size="pageSize" background layout="prev, pager, next" :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: {
    code: Number,
    pageSize: Number,
    total: Number,
    currentChange: Function,
  },
  data () {
    return {
    };
  },

  components: {},

  methods: {
    change (val) {
      //
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      this.currentChange(val);
    }
  },

  computed: {},

  // - this
  created () { },

  // - DOM
  mounted () { },
}

</script>
<style scoped>
.page-wrap {
  text-align: center;
  font-size: 14px;
  margin: 15px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>



