<!--  -->
<template>
  <div class="rec_list" ref="recList">
    <template v-for="(item,index) in dataList">
      <div class="item" :key="index">
        <!--  -->
        <slot :item="item" name="cover">
          <div class="cover_out">
            <div class="cover_play">
              <span class="play icon_play" @click="goPage(pageType,item[idPropName])">
                <i class="iconfont icon-icon_play_1"></i>
              </span>
            </div>
            <img class="cover pic" v-lazy="item.pic||item.img||defaultImg" lazy @error="imgError($event)">
            <!-- mv -->
            <slot :item="item" name="play_cnt">
            </slot>
          </div>
        </slot>
        <!--  -->
        <slot :item="item" name="info">

        </slot>
      </div>
    </template>
  </div>
</template>

<script>
import mixin from '../mixins';
export default {
  name: "MusicList",
  mixins: [mixin],
  props: {
    pageType: String,
    idPropName: String,
    dataList: Array,
  },
  data () {
    return {
      imgs: [],
      defaultImg: "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png",
    };
  },

  components: {},

  methods: {
