    //   this.imgs = this.$refs.recList.querySelectorAll('.cover');
    //   // 
    //   const clientH = document.documentElement.clientHeight;
    //   // 
    //   const clientT = document.documentElement.scrollTop || document.body.scrollTop;

    //   for (let i = 0; i < this.imgs.length; i++) {
    //     console.log(clientT)
    //     // + > 
    //     // !imgs[i].src 
    //     if (clientH + clientT > this.getTop(this.imgs[i])) {
    //       console.log('a' + this.imgs[i].dataset.src)
    //       if (this.imgs[i].src != this.imgs[i].dataset.src) {
    //         // data-xxdomdataset.xx
    //         this.imgs[i].src = this.imgs[i].dataset.src;
    //       }
    //     }
    //   }
    // },
    imgError (event) {
      event.currentTarget.src = this.defaultImg;
      event.currentTarget.onerror = null;
    }
  },

  watch: {
    // dataList: {
    //   handler () {
    //     setTimeout(this.lazyLoad, 100)
    //   }
    // }
  },
  computed: {},

  // - this
  created () { },

  // - DOM
  mounted () {
    // // 
