    // // 
    // window.onscroll = () => { this.lazyLoad() };
  },
}

</script>
<style lang="scss" scoped>
@import "../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.rec_list {
  display: flex;
  flex-wrap: wrap;

  .item {
    width: 18%;
    margin-right: 2%;
    margin-bottom: 26px;
    .cover_out {
      position: relative;
      overflow: hidden;
      width: 100%;
      padding-bottom: 100%;
      .cover_play {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background: transparent;
        z-index: 11;
        transition: all 0.2s ease-in-out;
        .play {
          position: absolute;
          top: 50%;
          left: 50%;
          background: #fff;
          border-radius: 50%;
          height: 50px;
          width: 50px;
          line-height: 50px;
          text-align: center;
          transform: translate(-50%, -50%);
          transition: all 0.2s ease-in-out;
          display: none;
        }
      }
      &:hover {
        .cover_play {
          background-color: rgba(5, 5, 5, 0.3);
        }
        .play {
          display: block;
        }
        .play .iconfont {
          font-size: 24px;
          color: #333;
        }
        .pic {
          // 
          transform: scale(110%);
        }
      }
      .pic {
        position: absolute;
        z-index: 10;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transition: transform 0.75s cubic-bezier(0, 1, 0.75, 1);
      }
    }
  }
}
</style>



