<!--  -->
<template>
  <div>
    <div class="artists">
      <music-list :dataList="artistData.artistList">
        <template v-slot:cover="props">
          <div class="pic_out">
            <img class="cover pic" v-lazy="props.item.pic300" @click="goPage('singer',props.item.id)" />
          </div>
        </template>
        <template v-slot:info="props">
          <p class="name flex_c">
            <span :title="props.item.name" class="text" v-html="props.item.name" @click="goPage('singer',props.item.id)"></span>
          </p>
          <p class="num">{{ props.item.musicNum }}</p>
        </template>
      </music-list>
      <music-no-data :keyWord="keyWord" v-show="(artistData.code != 200) & (artistData.code != 0)"></music-no-data>
    </div>
    <pagination :code="artistData.code" :currentChange="artistData.getData" :pageSize="artistData.pageSize" :total="artistData.total"></pagination>
  </div>
</template>

<script>
import mixin from '../mixins';
import MusicList from "./MusicList.vue";
import MusicNoData from "./MusicNoData.vue";
import Pagination from "./Pagination.vue";

export default {
  name: "SingerList",
  mixins: [mixin],
  props:
  {
    artistData: {
      type: Object,
      default: () => { return { code: 0 } }
    },

    keyWord: String,
  },
  data () {
    return {
      // data: {
      //   artistList: [],
      // },
    };
  },

  components: {
    MusicNoData,
    MusicList,
    Pagination
  },

  methods: {
    imgError (event) {
      event.currentTarget.src = "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png";
      event.currentTarget.onerror = null;
    }
  },

  computed: {
    // data () {
    //   return Object.assign({}, that.artistData, {})
    // }
  },
  watch: {
    // 'artistData.artistList': {
    //   handler (newVal, oldVal) {
    //     this.data = this.artistData;
    //     console.log(this.data)
    //   },
    //   immediate: true, //
    //   // deep: true,
    // }
  },
  // - this
  created () { },

  // - DOM
  mounted () {
    // this.data = this.artistData;
  },
}

</script>
<style lang='scss' scoped>
.artists {
  text-align: center;
  .pic_out {
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    overflow: hidden;
    border-radius: 50%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .name {
    position: relative;
    height: 22px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    flex-wrap: nowrap;
    justify-content: center;
    padding: 0 5px;
    .text {
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      overflow: hidden;
      display: block;
    }
  }
  .num {
    margin-top: 5px;
    font-size: 13px;
    color: #999;
  }
}
</style>



