<!--  -->
<template>
  <div class="nodata flex_c">
    <div class="inner">
      <img src="https://h5static.kuwo.cn/www/kw-www/img/introduce_grey.f18848f.png" class="nodata_img">
      <div class="tip">
        <p>
          <span v-if="keyWord!=null"> "<span class="col_pri">{{keyWord}}</span>" </span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MusicNoData",
  props: {
    keyWord: String,
  },
  data () {
    return {
      state: 0
    };
  },

  components: {},

  methods: {},

  computed: {},
  watch: {
    code: {
      handler (newVal, oldVal) {
        this.state = newVal;
      },
      immediate: true, //
    }
  },
  // - this
  created () { },

  // - DOM
  mounted () { },
}

</script>
<style lang="scss" scoped>
.nodata {
  width: 100%;
  padding: 100px 0;
  justify-content: center;
  .inner {
    text-align: center;
    .tip {
      text-align: center;
      word-break: break-all;
      line-height: 22px;
      color: #666;
      .col_pri {
        font-weight: 400;
      }
    }
  }
  .nodata_img {
    width: 260px;
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>



