<!--  -->
<template>
  <div>
    <div class="playlist">
      <music-list :dataList="playlistData.playlist" pageType="playlist" idPropName="id">
        <template v-slot:info="props">
          <p class="name">
            <span :title="props.item.name" @click="goPage('playlist',props.item.id)" v-html="props.item.name"></span>
          </p>
          <p class="count">
            <i class="iconfont icon-icon_play_"></i>
            {{ props.item.listencnt|playCnt }}
          </p>
        </template>
      </music-list>
      <music-no-data :keyWord="keyWord" v-show="playlistData.code!=200 & playlistData.code!=0"></music-no-data>
    </div>
    <pagination :code="playlistData.code" :currentChange="playlistData.getData" :pageSize="playlistData.pageSize" :total="playlistData.total"></pagination>
  </div>
</template>

<script>
import mixin from '../mixins';
import MusicList from "./MusicList.vue";
import MusicNoData from "./MusicNoData.vue";
import Pagination from "./Pagination.vue";
export default {
  name: "Playlist",
  mixins: [mixin],
  props: {
    playlistData: {
      type: Object,
      default: () => ({ code: 0 })
    },
    keyWord: String,
  },
  data () {
    return {
    };
  },

  components: {
    MusicNoData,
    MusicList,
    Pagination
  },

  methods: {},

  computed: {},
  filters: {
    playCnt (val) {
      let num = parseInt(val);
      if (num < 10000) {
        return num;
      } else {
        return (num / 10000).toFixed(1) + ""; //
      }
    }
  },
  // - this
  created () { },

  // - DOM
  mounted () { },
}

</script>
<style lang="scss" scoped>
.playlist {
  .name {
    margin-top: 10px;
    font-size: 16px;
    line-height: 22px;
    height: 44px;
    font-weight: 400;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
    span {
      cursor: pointer;
    }
  }
  .count {
    color: #999;
    margin-top: 5px;
    font-size: 14px;
  }
}
</style>
<style lang="scss">
.mvs {
  .item {
    .cover_out {
      padding-bottom: 59% !important;
    }
  }
}
</style>



